.about-me-text-container {
  display: flex;
  flex-direction: row;
  justify-content: center; /* Center horizontally */
  align-items: center;
  margin-top: 20px;
}

.about-text {
  font-size: 36px; /* Default font size */
  font-weight: bold;
}

.me-text {
  font-size: 36px; /* Default font size */
  margin-left: 10px; /* Add margin between "About" and "Me" text */
  font-weight: bold;
  color: purple;
}

.info-container {
  margin-top: 50px;
  display: flex;
  justify-content: center; /* Center horizontally */
}

.profile {
  width: 350px;
  height: 350px;
  border-radius: 10%;
}

.profile-container {
  margin-left: 20px;
}

.info {
  margin-left: 50px;
  max-width: 600px;
  position: relative;
}

.info:hover::after {
  /* Create a pseudo-element that will be used for the wavy animation */
  content: "";
  position: absolute;
  bottom: -10px; /* Adjust the value to control the height of the waves */
  left: 0;
  right: 0;
  height: 10px; /* Adjust the value to control the height of the waves */
  background-image: linear-gradient(
    to right,
    #fff,
    #000
  ); /* Adjust colors as needed */
  opacity: 0.5; /* Adjust opacity as needed */
  animation: wave 1s infinite linear; /* Set the animation properties */
}

@keyframes wave {
  0% {
    transform: translateX(-100%); /* Move the wave to the left */
  }
  100% {
    transform: translateX(100%); /* Move the wave to the right */
  }
}

.info h2 {
  font-weight: 700;
  transition: transform 0.3s ease-in-out;
}

.info h2:hover {
  animation: wave 0.5s infinite; /* Apply the wave animation on hover */
  color: orangered;
}

@keyframes wave {
  0% {
    transform: translateY(0); /* Set the initial position of the text */
  }
  50% {
    transform: translateY(
      -10px
    ); /* Set the maximum position of the text during the wave animation */
  }
  100% {
    transform: translateY(
      0
    ); /* Set the final position of the text to return to its original position */
  }
}

.info h5 {
  font-weight: 700;
}

.info h6 {
  margin-top: 15px;
}

@media (max-width: 768px) {
  .info-container {
    margin-top: 50px;
    flex-direction: column;
    /* display: flex; */
    justify-content: center; /* Center horizontally */
    align-items: center;
  }

  .profile {
    width: 250px;
    height: 250px;
  }

  .info {
    margin-top: 30px;
    max-width: 300px;
    margin-left: -0px;
  }
}

.resume-button {
  margin-top: 30px;
}

.button {
  --width: 100px;
  --height: 35px;
  /* --tooltip-height: 35px; */
  /* --tooltip-width: 90px; */
  --gap-between-tooltip-to-button: 18px;
  --button-color: linear-gradient(90deg, #4e54c8 0%, #8f94fb 100%);;
  /* --tooltip-color: #fff; */
  width: var(--width);
  height: var(--height);
  background: var(--button-color);
  position: relative;
  text-align: center;
  border-radius: 0.45em;
  font-family: "Arial";
  transition: background 0.3s;
  cursor: pointer;
}

.button::before {
  position: absolute;
  content: attr(data-tooltip);
  width: var(--tooltip-width);
  height: var(--tooltip-height);
  background-color: var(--tooltip-color);
  font-size: 0.9rem;
  color: #111;
  border-radius: 0.25em;
  line-height: var(--tooltip-height);
  bottom: calc(var(--height) + var(--gap-between-tooltip-to-button) + 10px);
  left: calc(50% - var(--tooltip-width) / 2);
}

.button::after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-top-color: var(--tooltip-color);
  left: calc(50% - 10px);
  bottom: calc(100% + var(--gap-between-tooltip-to-button) - 10px);
}

.button::after,
.button::before {
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
}

.text {
  display: flex;
  align-items: center;
  justify-content: center;
}

.button-wrapper,
.text,
.icon {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  color: #fff;
}

.text {
  top: 0;
}

.text,
.icon {
  transition: top 0.5s;
}

.icon {
  color: #fff;
  top: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon svg {
  width: 24px;
  height: 24px;
}

.button:hover {
  background: #6c18ff;
}

.button:hover .text {
  top: -100%;
}

.button:hover .icon {
  top: 0;
}

.button:hover:before,
.button:hover:after {
  opacity: 1;
  visibility: visible;
}

.button:hover:after {
  bottom: calc(var(--height) + var(--gap-between-tooltip-to-button) - 20px);
}

.button:hover:before {
  bottom: calc(var(--height) + var(--gap-between-tooltip-to-button));
}
